.sidebar {
  &.sidebar-dark {
    background: $navbar-vertical-dark-bg;
    .navbar-brand svg {
      color: $white;
    }
    .sidebar-header {
      border-bottom-color: tint-color($navbar-vertical-dark-bg, 10%);
    }
    .nav-item {
      .nav-link {
        &:not(.active) {
          color: $white;
        }
      }
    }
    &.sidebar-default {
      .nav-link {
        --shadow-dark-color: #{$white};
        &.active {
          background-color: var(--#{$variable-prefix}primary);
          box-shadow: 0 10px 20px -10px rgba(var(--shadow-dark-color), 38%);
        }
        &:hover {
          &:not(.active) {
            color: var(--#{$variable-prefix}primary);
          }
        }
      }
      .navbar-nav {
        .nav-item {
          .nav-link {
            &:not(.disabled)[aria-expanded="true"] {
              color: $white;
            }
          }
        }
      }
    }
    &.sidebar-base {
      .nav-item {
        &.static-item {
          .default-icon {
            color: $white;
          }
        }
      }
    }
    
  }
}   
